import React from 'react'
import { useContext } from "react";
import { AppContext } from "../../context/auth.context";
import { Link } from "react-router-dom";
import "./Navbar.css";

const Navbar = () => {
  const { 

    user,handleLogOut,  myRecipe
  }  = useContext(AppContext)



  return (
    <nav>
      <Link to="/" style={{ textDecoration: 'none' }} className="nav__projectName">
        <h1 className='snitch'>foodSnitch</h1>
      </Link>

      <div className="nav__authLinks">
        {user ? (

          
          <>
          
       
           <Link to="/recipes" className="authLink">
              all recipes
            </Link>
            <div className='myRecipe'>
            <Link id='fa' to="/myrecipes" className="authLink">
            <p>my recipes</p> <p id='length'>{myRecipe.length}</p></Link>
            </div>
           
            <Link to="/randomrecipes" className="authLink">random recipes
            </Link>
            <button className="nav-logoutbtn" onClick={handleLogOut} >
              logout
            </button>
          </>
        ) : (
          <>
          
          <div className='signup'>
            <Link to= "/signup" id='signup' className="authLink">
              sign up
            </Link>
            </div>
            <div className='login'>
            <Link to="/signin" id='login' className="authLink">
              log in
            </Link>
          </div>
          </>
        )}
      </div>
    </nav>
  );
};

export default Navbar;
